

import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mavonEditor} from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import axios from 'axios';
export default ({
components:{
Group,XTextarea,mavonEditor,XButton,Toast
},
data:function(){
return{
title:'',
content:'',
toastText:'',
isShowToast:false ,//
toolbars: {
bold: true, // 

header: true, // 
underline: true, // 
strikethrough: true, // 
mark: true, // 
superscript: true, // 
subscript: true, // 
quote: true, // 
ol: true, // 
ul: true, // 
link: true, // 
imagelink: true, // 
code: true, // code
table: true, // 
fullscreen: true, // 
readmodel: true, // 
htmlcode: true, // html
help: true, // 
/* 1.3.5 */
undo: true, // 
redo: true, // 
trash: true, // 
save: false, // eventssave
/* 1.4.2 */
navigation: true, // 
/* 2.1.8 */
alignleft: true, // 
aligncenter: true, // 
alignright: true, // 
/* 2.2.1 */
subfield: true, // 
preview: true 
}
}
},
methods:{
uploadImage:function(pos,file){



var app = this;
var params = new FormData();
params.append('smfile',file);
axios.post('https://sm.ms/api/v2/upload',params,{

headers:{'Content-type':'multipart/from-data'}
}).then(function(res){

console.log(res.data);
app.$refs.md.$img2Url(pos,res.data.message.url)
})
},
addtopic:function(){
var app = this;
this.$http.post('/topics',{
accesstoken:this.$store.state.accesstoken,
title:this.title,
content:this.content,
tab:'dev'
}).then(function(res){
console.log(res);
app.$router.replace('/topiclist/dev');
}).catch(function(error){

app.isShowToast=true
app.toastText='';
})
}
}
})



